.main-header {
    width: 100vw;
    height: calc(100vh - 3rem);
    display: grid;
    place-items: center;
    margin-top: 3rem;
}

.main-header-container {
   display: grid;
   height: 100%;
   grid-template-columns: 1fr 1fr; 
   align-items: center;
   justify-content: space-between;
   gap: 6rem;
}

.main-header-left {
    margin-top: -3rem;
}

.main-header-left h4 {
    margin-bottom: 1rem;
    color: var(--secondary-color);
    font-weight: 400;
}

.main-header-left p {
    margin: 1rem 0 3.5rem;
    font-size: 1.1rem;
}

.main-header-right {
    display: grid;
    place-items: center;
    position: relative;
}

.main-header-circle {
    width: 25rem;
    height: 25rem;
    background: linear-gradient(75deg, var(--secondary-color), transparent);
    border-radius: 50%;
    position: absolute;
    filter: blur(15px);
    transition: var(--transition);
}

.main-header-image {
    position: relative;
}
/* 
.main-header-right:hover .main-header-circle {
    filter: blur(15px)
} */



/* programs */

.programs {
    margin-top: 4rem;
}

.programs-wrapper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 3rem;
    margin-top: 4rem;
}

.programs-program a {
    margin-top: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.programs-program:hover a {
    background: var(--secondary-color);
    color: var(--gray-color-600);
}



/* Values */

.values-container {
    display: grid;
    grid-template-columns: 38% 50%;
    gap: 12%;
}

.values-image {
    filter: saturate(0.25);
    transition: var(--transition);
}

.values-image:hover {
    filter: saturate(1);
}

.values-right > p {
    margin: 1.5rem 0 5rem;
}

.values-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 3.4rem 2.4rem;
}

.card.values-value {
    padding-top: 3.5rem;
    text-align: left;
    position: relative;
}

.card.values-value span {
    position: absolute;
    top: -1.5rem;
}






/* Faqs */

.faqs-wrapper {
    margin-top: 4rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem 5rem;
    align-self: start;
}

.faq {
    background: var(--gray-color-500);
    padding: 2rem;
    border-radius: 1rem;
    /* cursor: pointer; */
}

.faq div {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.faq-icon {
    background: transparent;
    color: var(--gray-color-100);
    font-size: 1.5rem;
    cursor: pointer;
}

.faq-icon:hover {
    
    color: var(--secondary-color);
}

.faq p {
    margin-top: 1.5rem;
}







/* Testimonials section */
.testimonials {
    width: 50rem;
    margin-inline: auto;
}

.testimonials-head {
    justify-content: center;
}

.card.testimonial {
    position: relative;
    text-align: left;
    margin-top: 5rem;
}

.testimonial-avatar {
    width: 4rem;
    height: 4rem;
    border-radius: 1.5rem;
    border: 2px solid transparent;
    overflow: hidden;
    position: absolute;
    top: -2rem;
    left: calc(50% - 2rem);
    box-shadow: 0 1.5rem 2rem rgba(26, 26, 54, 0.4);
    transition: var(--transition);
}

.testimonial:hover .testimonial-avatar {
    border-radius: 50%;
    border-color: var(--gray-color-400);
}

.testimonial-quote {
    font-style: italic;
    margin: 1rem 0 2rem;
}

.card small.testimonial-title {
    margin-top: 0.3rem;
}

.testimonials-btn-container {
    width: fit-content;
    margin: 2.5rem auto 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

.testimonials-btn {
    background: transparent;
    cursor: pointer;
    transition: var(--transition);
}

.testimonials-btn svg {
    fill: var(--primary-color);
    font-size: 1.8rem;
    width: 3rem;
    height: 3rem;
}

.testimonials-btn:hover svg { 
    fill: var(--secondary-color);
}




/* footer section */

footer {
    background: var(--primary-color);
    margin-top: 7rem;
    padding-top: 7rem;
    font-size: 0.9rem;
    color: var(--gray-color-100);
}

.footer-container {
    display: grid;
    grid-template-columns: 26rem 1fr 1fr 1fr;
    gap: 6rem;
} 

.footer-container article {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.7rem;
}

.footer-container article p {
    margin-top: 0.5rem;
}

.footer-container article h4 {
    margin-bottom: 0.6rem;
    font-size: 1rem;
}

.footer-socials {
   margin-top: 1rem;
   display: flex;
   gap: 1rem;
   align-items: center; 
}

.footer-socials a {
    background: var(--gray-color-100);
    padding: 0.7rem;
    border: 1px solid transparent;
    border-radius: 0.6rem;
    transition: var(--transition);
}

.footer-socials a svg {
    color: var(--gray-color-600);
}

.footer-socials a:hover {
    border-color: var(--gray-color-100);
    background: transparent;
}

.footer-copyright {
    color: var(--gray-color-100);
    text-align: center;
    padding: 1.5rem 0;
    border-top: 2px solid var( --primary-color-variant);
    margin-top: 5rem;
}


/* media styles goes here */

@media (max-width: 1024px) {
    .main-header {
        /* height: auto; */
        height: fit-content;
        padding: 12rem 0;
    }

    .main-header-container {
        gap: 0;
    }

    .main-header-circle {
        width: 16rem;
        height: 16rem;
    }


    /* programs */
    .programs-wrapper {
        grid-template-columns: 1fr 1fr;
    }


    
    /* values */
    .values-container {
        grid-template-columns: 1fr;
        justify-content: center;
        gap: 4rem;
    }

    .values-wrapper {
        gap: 4rem 3rem;
    }

    /* .values-image {
        display: none;
    } */




    /* FAQs */

    .faqs-wrapper {
        grid-template-columns: 1fr;
    }



    /* Testimonials */

    .testimonials {
        width: 75%;
    }


    /* footer */

    .footer-container {
        grid-template-columns: 1fr 1fr;
        gap: 4rem;
    }
}




@media (max-width: 600px) {
   /* main header */
   .main-header {
    margin-top: 0;
    padding: 12rem 0 0;
    height: 100vh;
   }

   .main-header-image, .main-header-circle {
    display: none;
   }

   .main-header-container {
    grid-template-columns: 1fr;
   }

   /* programs */
   .programs-wrapper {
    grid-template-columns: 1fr;
    margin-top: 3rem;
    gap: 2rem;
   }

   .programs-program {
    width: 84%;
    margin: 0 auto;
   }


   /* values */
   .values-wrapper {
    grid-template-columns: 1fr;
    gap: 3rem;
   }

   .card.values-value {
    width: 84%;
    margin-inline: auto;
   }


   /* FAQs */
   .faqs-wrapper {
    gap: 1rem;
    margin-top: 3rem;
   }


   /* Testimonail */
   .testimonials {
    width: 100%;
   }


   /* footer */

   .footer-container {
    grid-template-columns: 1fr;
    gap: 3rem;
   }

   .footer-container article {
    align-items: center;
   }

   
   .footer-container article p {
    text-align: center;
   }

}